/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

.skip-container, #header_section {
}

#header_section {

    
    @include respond-to(handhelds) {
        padding:0;
        height:auto;
    }
}

#logo {
    border-top:0;
    

}

#top-search {
        @include respond-to(handhelds) {display:none}

    #searchform {
    float:right;
    margin-top:85px;
    margin-right:4em;
        
        input {
            box-shadow: none;
            border-radius:0;
            height:35px;
            margin:0;
        }
        
        .submit {

            background-position: center;
            background-size: contain;
            background-repeat:no-repeat;
            height:35px;
            width:35px;
            margin-left:-4px;
            margin-top:1px;
        }
    }
}
