/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

// Main navigation

.main-nav {

    background-color:transparent;
    background-image:none;
    float:left;
    clear:none;
    margin-top:90px;
    border:none;
    border-image:none;
    
    @include respond-to(handhelds) {
        margin-top:0;   
    }

    .menu {
        background-color:transparent;
        background-image:none;
        border:none;
        display:inline;

        li {
            border: none;  
            display:inline;
            float:left;
            background-color:transparent;

            &:hover, &:active {
                background-image:none;
                background-color:transparent;
            }

            a {
                border:none;
                font-size:.8em;
                text-shadow:none !important;

                &:active, &:hover {
                    background-image:none;
                    background-color:transparent;
                }

            }
        }  
        
        .current-menu-item a, .current-page-item a {
            background-image:none;
            background-color:transparent;
        }



    } 
    
    @include respond-to(handhelds) {
        width:100%;
        background-image:none;
        border:none;
        
        .menu {
            background-color:transparent;
            background-image:none;
            border:none;
            
            li {
                background-image:none;
                margin:none;
                display:block;

                
                a {
                    background-image:none;
                    border:none !important;
                    text-align: center !important;
                    text-transform:uppercase;

                    
                    &:active {
                        background-image:none;
                        border:none; 
                    }
                    
                }
                
            }
            
            .current-menu-item a, .current-page-item a {
            background-image:none;
        }
        }
        
        #responsive_current_menu_item {
            background-image:none;

        }
        
        #responsive_menu_button {
            border-image:none !important;
            border:none !important;
        }
        
    }


}


